@font-face {
 
  font-family: "Moderat"; 
  src: url("/src/font/Moderat.woff2") format("woff2"); 
  font-style: italic; 
  font-weight: normal; 
  font-style: normal;

  } 
  @font-face {
 
    font-family: "Steinbeck"; 
    src: url("/src/font/Steinbeck.otf") format("OpenType"); 
    font-style: italic; 
    font-weight: normal; 
    } 
html{
	scroll-behavior: smooth;
}
body{
	position: relative;
	max-width: 375px;
	width: 100%;
	height: 100%;
	background: #FFFFFF;
	margin: auto;

}
h1{
	font-family: Steinbeck;
	font-style: normal;
	font-weight: normal;
	font-size: 32px;
	line-height: 36px;
	text-align: center;
}
p{
	padding: 0;
	margin: 0;
}
.container{
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;

}
.head__phone{
	display: flex;
	justify-content: space-evenly;
	align-items: flex-end;
	position: sticky;
	top: 0;
	background: rgba(255, 255, 255, 0.8);
	backdrop-filter: blur(20px);
	z-index: 9999999999999;
}

.right__side{
	display: flex;
	flex-basis: 66.66px;
	justify-content: space-around;
}
.left__side{
	display: flex;
	flex-basis: 66.66px;
}
.right__side > div{
	display: inherit;
	justify-content: center;
	align-items: center;
	flex-basis: 66.66px;
}

.logo{
	padding: 64px 281px 60px 20px;
}

.title{
	width: 335px;
	height: 108px;
	left: 20px;
	top: calc(50% - 108px/2 - 512.5px);
	font-family: Steinbeck;
	font-style: normal;
	font-weight: normal;
	font-size: 32px;
	line-height: 36px;
	text-align: center;
	color: #000000;
	padding: 0 20px;
	font-family: Steinbeck;
	font-size: 32px;
	font-style: normal;
	font-weight: 400;
	line-height: 36px;
	letter-spacing: 0px;
	text-align: center;

}
 .title:nth-child(2){
	 font-style: italic;
 }
 .two_columns_text > div{
  font-family: Moderat;
  font-style: normal;
  font-weight: normal;  
  font-weight: bold;

 }
.list__dt{
	padding: 24px 20px 40px 20px;
	display: flex;
	width: 335px;
	height: 20px;
	font-family: Moderat;
	font-style: normal;
	font-weight: normal;
	font-size: 15px;
	line-height: 20px;
	justify-content: center;
	color: rgba(0, 0, 0, 0.7);
}
.block__info{
	display: flex;
	flex-direction: column;
	margin: auto;
}

.block__info__element{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	position: static;
	width: 335px;
	height: 80px;
	left: 0px;
	top: 176px;
	background: rgba(0, 0, 0, 0.03);
	border-radius: 12px;
	flex: none;
	order: 2;
	flex-grow: 0;
	margin: 8px 0px;
}


.block__element__container{
	display: flex;
	flex-wrap: nowrap;
	align-items: flex-start;
	justify-content: flex-start;
}


.block__element__numberpt >div {

	display: flex;
	flex-direction: column;
	background: #23786E;
	width: 26px;
	height: 26px;
	justify-content: center;
	align-items: center;
	border-radius: 50%;	

	font-family: 'Moderat';
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    line-height: 14px;
    display: flex;
    align-items: center;
    letter-spacing: 0.2px;
    color: #FFFFFF;
}
.block__element__numberpt {
	padding: 20px 293px 16px 20px;
	position: absolute;
}
.block__element__span{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding: 20px 20px 26px 58px;
	width: 335px;
	height: 80px;
	left: 0px;
	top: 176px;
	width: 257px;
	height: 40px;
	left: 42px;
	top: 0px;
	font-family: 'Moderat';
	font-style: normal;
	font-weight: normal;
	font-size: 15px;
	font-style: normal;
	font-weight: 400;
	line-height: 20px;
	letter-spacing: 0em;
	text-align: left;

}


.line{
	border: 2px dashed #EDEDED;
	margin: 50px 0 0 0;
	height: 0px;
	height: 0px;
	width: 327px;
	left: 28px;
	top: 958px;
	border-radius: nullpx;
}
.two_columns_text {
    display: flex;
    justify-content: space-between;
	 font-size: 15px;
	 line-height: 20px;

}
.btn_block{
		display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
.btn{
	padding: 15px 32px;
	width: 335px;
	height: 56px;
	background: #257E74;
	border-radius: 12px;
	flex: none;
	order: 0;
	align-self: stretch;
	margin: 10px 0px;
	font-family: 'Moderat';
	font-style: normal;
	font-weight: bold;
	font-size: 15px;
	line-height: 20px;
	color: #FFFFFF;
}
.tab__bar{
position: sticky;
bottom: 0;
background: rgba(255, 255, 255, 0.8);
backdrop-filter: blur(20px);
padding: 10px 0px 0px;

height: 133px;
left: 0px;
right: 0px;
bottom: 0px;
}
.search__tab{
	display: flex;
	justify-content: space-around;
	position: static;
	width: 323px;
	height: 40px;
	left: calc(50% - 323px/2);
	top: 0px;
	margin: auto;
	align-items: center;
	background: #FFFFFF;
	box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
	border-radius: 10px;
	flex: none;
	order: 0;
}
.search__tab__text {
	display: flex;
	flex-basis: 70%;
	justify-content: center;
	align-items: center;
}
.search__tab__text__icon {
	display: flex;
	flex-basis: 5%;
}
.tab__bar__nav__icon__container {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: flex-end;
	 padding: 12px 0 0 0;
}
.tab__bar__nav__icon{
	flex-basis: 20%;
	display: flex;
	justify-content: center;
}

.home__indicator {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    flex-wrap: nowrap;
	 padding: 33.42px 0 8px 0;
}
 .title__italic{
  font-style: italic;
 }



